:root {
    --header-color: #B2ABF2;
    --body-color: #FFEDE1;
    --black-color: black;
    --white-color: white;
    --home-bb-color: #363537; 
    --home-color: #EBEBD3;
    --content-color: #3BF4FB;
    --bis-color: bisque;
    --yellow-color: #CAF500;
}

body {
    background-color: #AEE5D8;
}

header {
    background-color: #AEE5D8;
    position: absolute; 
    height: 70px; 
    display: flex;
    flex-direction: column;
}

#main-header {
    padding: 10px 10px 0;
    margin: 0 auto;
    background-color: #AEE5D8;
}

#app-name {
    text-align: center;
    padding: 2%;
    font-size: 24px; 
}

#main-nav {
    position: absolute;
    display: flex;
    justify-content: space-around; 
    align-items: center;
    background-color: #AEE5D8;
    width: 100%; 
    height: 80px; 
    margin: 0; 
}

#main-nav ul {
    padding-left: 0;
    display: flex; 
    width: 100%; 
    justify-content: space-around; 
}
button.nav-user  img{
    width:40px;
}

#h-title {
    margin-bottom: 5px; 
}

#h-title img {
    margin-left: 41%;
    margin-top: 2%;
    margin-bottom: 2%;
    width: 15%;
}

#main-nav li {
    list-style-type: none;
    flex: 1; 
    text-align: center; 
    padding: 5px;
}

#main-nav a {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-decoration: none;
    color: var(--black-color);
    padding: 5px;
}

#main-nav a img {
    width: 40px;
    height: auto;
    margin-bottom: 5px;
    text-shadow: 5px 5px 15px rgba(0, 0, 0, 0.3);
}

#main-nav .nav-text {
    color: black;
    font-size: 14px;
}

#main-nav a:hover .nav-text {
    display: block;
}

label.toggle {
    text-align: center;
    width: auto;
    justify-content: center;
}

#h-ul {
    display: flex;
    margin: 1px;
    position: relative;
    top: 5%;
    right: 5%;
}

#h-logout {
    position: absolute; 
    top: 10px; 
    right: 10px; 
    z-index: 0; 
    background-color: transparent;

}

button.nav-user {
    background-color: transparent;
    border: none;
    width: 100px;
}

@media screen and (max-width: 768px) {
    #main-nav ul {
        display: none;
        flex-direction: column;
        background-color: #AEE5D8;
    }
    
    #main-nav img {
        display: none;
    }
    
    .hamburger {
        display: flex;
        cursor: pointer;
        flex-direction: column;
        gap: 5px;
    }

    .hamburger-icon {
        width: 25px;
        height: 5px;
        background-color: #333;
        border-radius: 2px;
    }
    
    #main-nav ul.show {
        display: block;
    }

    #main-nav {
        height: auto;
    }
}

@media screen and (min-width: 769px) {
    #main-nav ul {
        display: flex;
        flex-direction: row;
        background-color: #AEE5D8;
    }
    
    .hamburger {
        display: none; 
    }
}
