:root {
    --header-color: #B2ABF2;
    --body-color: #FFEDE1;
    --black-color: black;
    --white-color: white;
    --home-bb-color: #B2ABF2;
    --home-color: #EBEBD3;
    --content-color: #3BF4FB;
    --bis-color: bisque;
    --yellow-color: #CAF500;
    --upload-bg-color: #f9f9f9; 
    --upload-border-color: #ccc; 
    --upload-button-bg-color: #000000; 
    --upload-button-hover-bg-color: #218838; 
}

#upload-bottom {
    position: relative;
    top: 380px;
    max-width: 600px; 
    margin: 20px auto; 
    padding: 20px;
    border: 1px solid var(--upload-border-color); 
    border-radius: 5px; 
    background-color: var(--upload-bg-color); 
}

@media screen and (max-width: 768px) {
   #upload-bottom {
       position: relative;
       top: 300px;
   }
}
@media screen and (min-width: 1600px) {
    #upload-bottom {
        position: relative;
        top: 300px;
    }
 }
#upload-bottom h2 {
    text-align: center;
}

#upload-bottom div {
    margin-bottom: 15px; 
}

#upload-bottom label {
    display: block; 
    margin-bottom: 5px; 
}

#upload-bottom select,
#upload-bottom input,
#upload-bottom textarea {
    width: 100%;
    padding: 10px; 
    border: 1px solid var(--upload-border-color); 
    border-radius: 4px; 
}

#upload-button {
    width: 100%; 
    padding: 10px; 
    background-color: var(--upload-button-bg-color); 
    color: var(--white-color); 
    border: none; 
    border-radius: 5px; 
    cursor: pointer; 
}

#upload-button:hover {
    background-color: var(--upload-button-hover-bg-color); 
}
